body {
	padding: 0;
	margin: 0;

	}
div{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
}
* div{
	font-family: Helvetica;
	}
	.wrapper{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-direction: column;
	}
.page{
		width: 98vw;
		background: #E5E5E5;
		flex-direction: column;
}
.logo{
	height: 65px;
	width: 55px;
	padding: 10px 4px 0 8px;
	background: #E5E5E5;
}
.header{
	-webkit-flex-direction: row;
	-ms-flex-direction: row;
	flex-direction: row;
	background: white;
	height: 75px;
	width: 100%;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-ms-align-items: center;
	align-items: center;
	justify-content: space-between;
}
.header__side{
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	align-items: center;
}
.header__name{
	margin-left: 30px;
}
.header__stat{
	margin-right: 130px;
}
.header__time{
	color: #6F6EFF;
	margin-right: 70px;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items:flex-end;
}
.header__ava{
	margin-right: 20px;
	border-radius: 50%;
}
main{
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.f-column{
	width: 475px;
	max-height: 880px;
	border-radius: 15px;
	margin: 66px 16px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	-ms-align-items: center;
	align-items: center;
}
.col_a{
		width: 337px;
}
.col_b{
justify-content: space-between;
}
.column__wrapper_a{
	width: 337px;
	height: 100%;
	border-radius: 2%;
	background: white;
	color: black;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	-ms-align-items: center;
	align-items: center;

}
.column__wrapper_a span{
	display: block;
	font-family: Helvetica;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 21px;
	margin-top: 50px;
}
.column__wrapper_a .active{
	border: 5px solid #6F6EFF;
 	border-style: none none none solid ;
	display: block;
	padding: 15px 0;
	width: 98%;
	text-align: center;
background: linear-gradient(170.57deg, #F2F2F8 0%, #FFFFFF 93.99%);
}
.camera_wrapper{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	background: #fff;
	border-radius: 15px;
	height: 278px;
	width: 395px;
	padding: 20px 30px;
	margin-bottom: 60px;
	}
	.car{
		width: 100%;
	}
	.camera_wrapper span {
		font-size: 18px;
		line-height: 16px;
		color: #5E5E7B;
		margin-bottom: 20px
	}
	.visited{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	background: #fff;
	border-radius: 15px;
	width: 395px;
	padding: 20px 30px;
	margin-bottom: 50px;	
}
.visited span {
		font-size: 18px;
		line-height: 16px;
		color: #5E5E7B;
		margin-bottom: 20px
	}
.visited img{
	margin-bottom: 10px;
}
.visited_header{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.space{
	margin-top: 64px;
}
.footer{
	flex-direction: row;
    background: white;
    width: 100%;
    display: -ms-flex;
    display: flex;
    -ms-align-items: center;
    align-items: center;
    justify-content: space-between;
	height: 4vh;
	min-height: 75px;
}